@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
* {
  box-sizing: border-box;
  margin: 0;
}


body {
  min-height: 2000px;
  box-sizing: border-box;
  font-family:  -apple-system, BlinkMacSystemFont, 'Roboto', Helvetica Neue,sans-serif;
  background: #f6f6f6;
}


.wrapper {
  padding: 0 10px;
  margin: 0 auto;
}

@media (min-width: 576px) {
  .wrapper {
    width: 550px;
    padding: 0 30px;
  }
}

@media (min-width: 768px) {
  .wrapper {
    width: 720px;
  }
}

@media (min-width: 992px) {
  .wrapper {
    width: 940px;
  }
}

@media (min-width: 1200px) {
  .wrapper {
    width: 1190px;
  }
}
.App {
  font-size: 85%;
  padding: 0;
}

nav, a {
  margin: 0px;
  padding: 0px;
  text-decoration: none;
}


.headerbg {
  background: #1e90ffad;
  margin-bottom: 20px;
  box-shadow: 0px 0px 8px #2d2d2d;
  width: 100%;
  min-height: 60px;
  display: flex;
  align-items: center;
}

header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  height: 100%;
}

nav {
  display: flex;
  align-items: center;
}

nav a {
  margin: 0 10px;
}

.github {
  height: 40px;
}

.codepen {
  height: 41px;
  padding-top: 2px;
}

.codesandbox {
  height: 40px;
  
}

.caption {
  font-size: 1.2rem;
  font-weight: 1;
  font-style: italic;
  margin: 0 5px;
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    padding: 10px 0;
  }
  nav {
    padding-top: 5px;
    margin: 0;
  }
}


.About {
  margin-bottom: 30px;
}

.titles {
  margin-bottom: 20px;
  display: flex;
}

.titles * {
  margin: 0 30px 0 0;
}


@media (max-width: 576px) {
  .titles * {
    margin: 0 8px 0 0;
  }
  .titles {
    font-size: 0.5rem;
  }
} 
@media (min-width: 577px) and (max-width: 768px) {
  .titles {
    font-size: 0.6rem;
  }
  .titles * {
    margin: 0 20px 0 0;
  }
}

h2 {
  margin-bottom: 10px;
}
.Card {
  min-width: 300px;
  width: 350px;
  height: 300px;
  margin: 10px;
  background: white;
  box-shadow: 1px 1px 4px #3a3a3a;
}

@media (max-width: 992px) {
  .Card {
    width: 500px;
    margin: 5px;
  }
}

.Card__top {
  height: 70%;
  padding: 8px 8px 0 8px;
  /* dynamic background styling in component's jsx */
}

.Card__screenshot {
  height: 100%;
  box-shadow: inset 0px 0px 4px #424242;
}

.Card__description {
  padding: 10px;
  height: 100%;
  color: white;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  font-size: 120%;
}

.Card__description:hover {
  opacity: 1;
  -webkit-transition: opacity 0.1s linear 0.1s;
  transition: opacity 0.1s linear 0.1s;
}

.Card__bottom {
  padding: 5px 8px 8px 8px;
  height: 30%;
  overflow: hidden;
}

.Card__bottom a {
  opacity: 0.8;
}

.Card__bottom a:hover {
  opacity: 1;
}

.Card__title {
  margin: 4px 0 4px 0;
}

.Tags__list {
  height: 30px;
}

.Tag {
  opacity: 0.85;
  margin-bottom: 5px;
  -webkit-filter: saturate(90%);
          filter: saturate(90%);
  
}

.Tag:hover {
  cursor: pointer;
  opacity: 1;
  -webkit-filter: none;
          filter: none;
  box-shadow: 1px 1px 1px #595959;
}
.ProjectsList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  min-height: 500px;
}

.SearchSection {
  display: flex;
  flex-wrap: wrap;
  align-items: baseline;
  justify-content: flex-start;
  align-content: center;
  margin-bottom: 15px;
}

@media (max-width: 768px) {
  .SearchSection {
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    margin: 0 auto;
  }
  .SearchForm {
    margin: 0 auto 10px;
  }
  .showing {
    margin: 0 auto;
  }
}

.SearchForm {
  border-bottom: 1px solid #2d2d2d;
  min-width: 280px;
}

.SearchForm__input {
  font-size: 1.4rem;
  border: none;
  background: inherit;
  outline: none;
  padding: 5px 10px;
  min-width: 140px;
  max-width: 300px;
  color: tomato;
}

.xbutton {
  width: 20px;
}
.SearchForm__input::-webkit-input-placeholder {
  font-size: 1.4rem;
  color:#57abffad;
}
.SearchForm__input::-moz-placeholder {
  font-size: 1.4rem;
  color:#57abffad;
}
.SearchForm__input:-ms-input-placeholder {
  font-size: 1.4rem;
  color:#57abffad;
}
.SearchForm__input::-ms-input-placeholder {
  font-size: 1.4rem;
  color:#57abffad;
}
.SearchForm__input::placeholder {
  font-size: 1.4rem;
  color:#57abffad;
}

.showing {
  padding: 0 10px;
}

.Contact {
  margin-top: 50px;
}
